import React, { useEffect, useState } from 'react'
import Table from 'react-bootstrap/Table'
import { useNavigate, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { ADD, DLT, REMOVE } from '../redux/actions/action';

export default function CardsDetails() {

const [data,setdata] = useState([]);



const {id} = useParams();



const history = useNavigate();


const dispatch = useDispatch()


const getdata = useSelector((state) => state.cartreducer.carts);
  console.log(getdata)

  const compare= () =>{
    let compareData = getdata.filter((e) =>{
      return e.id == id
    });
    setdata(compareData);
  } 


   const send = (e) =>{
    dispatch(ADD(e));
   }

   const dlt=(id)=>{
    dispatch(DLT(id))
    history('/');
   }

   const remove = (item)=>{
    dispatch (REMOVE(item))
   }



  useEffect(()=>{
    compare();
  },[id])



  return (
    <div className='cardsdetails'>
      <div className='container mt-3'>
        <h2 className='text-center'> Items Details Page</h2>

        <section className='container mt-3'>
          <div className='iteamsdetails'>
          {
            data.map((ele)=>{
              return(
                <div>
                <div className='items_img'>
                <img src= {ele.image} alt="#" />
  
  
              </div>
              <div className='details'>
                 <table>
                 <tr>
                 <td>
                 <p><strong>Restuarnt</strong> : {ele.name}</p>
                 <p><strong>Price</strong> :     {ele.price}</p>
                 <p><strong>Dishes</strong> :   {ele.description}</p>
                 <p><strong>Total</strong> :     {ele.price * ele.qnty} </p>
                 <div className='mt-5 d-flex justify-content-between align-items-center' style={{width:100,cursor:"pointer",background:"#ddd",color:"#111"}}>
                    <span style={{fontSize:24}} onClick={ele.qnty <=1 ? ()=>dlt(ele.id) : ()=>remove(ele)}>-</span>
                    <span style={{fontSize:22}}>{ele.qnty}</span>
                    <span style={{fontSize:24}} onClick={()=>send(ele)}>+</span>

                    </div>
                 
                 
                 
                 </td>
                 <p><strong>Rating : </strong><span style={{background:"green",color:"#fff",padding:"2px 5px",borderRadius:"5px"}}>3.5 </span></p>
                 <p><strong>Order Review : </strong><span>1000+ order placed from here recently</span></p>
                 <p><strong>Remove : </strong><span  ><i className='fas fa-trash' onClick={()=>dlt(ele.id)} style={{color:"red", fontSize:20,cursor:'pointer'}}></i></span></p>
                 </tr>
                 
                 
                 
                 </table>
              </div>
  
  

                </div>
              )
            })
          }
            

          </div>




        </section>


      </div>





    </div>
  )
}
